import uniSegmentedControl from "@/components/uni/uni-segmented-control/uni-segmented-control.vue"
export default {
	components: {uniSegmentedControl},
	data() {
		return {
			getimg:this.$getimg,
			list:[],
			items: ['上架商品','下架商品'],
			current: 0
		}
	},
	onLoad() {
		this.get_pro()
		
	},
	onShow() {
		this.get_pro()
	},
	methods: {
		get_pro(){
			this.$api.http.post('product/mcms/all_goods_info').then(res=>{
				this.list = res.data
			})
		},
		_request(){
			this.list=this.$api.json_cms.my_product
		},
		pull(id){
			const that=this
			uni.showModal({
				title:'确定下架？',
				success(res){
					if(res.confirm==true){
						that.$api.http.put('/mcms/update',{
							id:id,
							db:'goods',
							field:'state'
						}).then(res=>{
							that.$api.msg('下架成功')
							that.get_pro()
						})  
						
					}
				}
			})				
		},
		del(id){
			const that=this
			uni.showModal({
				title:'确定删除？',
				success(res){
					if(res.confirm==true){
						that.$api.http.put('product/mcms/del_product',{
							id:id,
						}).then(res=>{
							that.$api.msg('删除成功')
							that.get_pro()
						})
					}
				}
			})
		},
		push(id){
			const that=this
			uni.showModal({
				title:'确定上架？',
				success(res){
					if(res.confirm==true){
						that.$api.http.put('/mcms/update',{
							id:id,
							db:'goods',
							field:'state'
						}).then(res=>{
							that.$api.msg('上架成功')
							that.get_pro()
						})
					}
				}
			})				
		},
		jump_edit(id){
			console.log(id)
			uni.navigateTo({
				url:'/pages/cms/edit/product/product?id='+id 
			})
		},
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		}
	},
	onPullDownRefresh() {
		this.get_pro()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
